@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: "Monserrat", sans-serif;
  background-color: #1a1a1c;
  color: #fff;
}

.coin-app img[src="./coinlogo1.png"] {
  width: 700px;
  height: 300px;
  margin-top: -50px;
}

.coin-table table,
tr,
th {
  border: 1px solid #ddd;
  text-align: left;
}

.coin-table table {
  border-collapse: collapse;
  width: 90%;
  table-layout: fixed;
  margin: auto;
}

.coin-table th,
tr {
  padding: 15px;
  border: 0;
  border-style: hidden;
  border-bottom: 1px solid #d7d7d7;
}

.coin-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: #fff;
}

.coin-search {
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.coin-text {
  margin-bottom: 32px;
  text-align: center;
  text-transform: uppercase;
  font-size: 40px;
}

.coin-search input::-webkit-input-placeholder {
  color: gray;
}

.coin-input {
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  background-color: white;
  color: #000000;
}

.coin-input::-webkit-input-placeholder {
  color: #e2e2e2;
}

.coin-input:-ms-input-placeholder {
  color: #e2e2e2;
}

.coin-input::-ms-input-placeholder {
  color: #e2e2e2;
}

.coin-input::placeholder {
  color: #e2e2e2;
}

/* Media Queries */

@media (max-width: 420px) {
  .coin-app img[src="./coinlogo1.png"] {
    width: 350px;
    height: 150px;
    margin-top: -25px;
  }

  .coin-input input[type="text"] {
    width: 100px;
    font-size: 20px;
    padding-left: 1rem;
  }
}

table,
td {
  border: 1px solid #ddd;
  text-align: left;
}

table {
  border-collapse: collapse;
  width: 90%;
  table-layout: fixed;
}

td {
  padding: 15px;
  border: 0;
  border-style: hidden;
  border-bottom: 1px solid #d7d7d7;
}

.coin-row {
  border: 0;
}

.coin-row h1 {
  font-size: 16px;
  width: 150px;
}

img {
  height: 30px;
  width: 30px;
  margin-right: 10px;
}

.coin-symbol {
  text-transform: uppercase;
}

.red {
  color: #f00606;
}

.green {
  color: #11d811;
}

/* Media Queries */

@media (max-width: 420px) {
  table,
  thead,
  tbody,
  th,
  td,
  tr {
    display: block;
    border: 0;
  }

  thead tr {
    position: absolute;
    top: -9999px;
    left: -9999px;
  }

  thead tr th .company-icon {
    top: -5px;
  }

  tr {
    border: 1px solid #ccc;
  }

  td {
    border: none;
    border-bottom: 1px solid #eee;
    position: relative;
    padding-left: 50%;
  }

  td:before {
    position: absolute;
    left: 6px;
    width: 45%;
    padding-right: 10px;
    white-space: wrap;
  }

  td:nth-of-type(1):before {
    content: "Company Icon";
    bottom: 18px;
  }
  td:nth-of-type(2):before {
    content: "Company Name";
  }
  td:nth-of-type(3):before {
    content: "Symbol";
    text-transform: none;
  }
  td:nth-of-type(4):before {
    content: "Current Price";
  }
  td:nth-of-type(5):before {
    content: "Total Volume";
  }
  td:nth-of-type(6):before {
    content: "Price Percentage Change";
    top: 6px;
  }
  td:nth-of-type(7):before {
    content: "Market Cap";
  }
}

